.App {
  margin: 0 auto;
  box-sizing: border-box;
}

.error {
  color: orangered;
}

.container {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.counter {
  width: 180px;
  height: 50px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #DBE9EE;
  border: 2px solid #93ACCA;
  border-radius: 5px;

}



